<template>
  <div class="overview" style="padding-left:20px;padding-right:20px">
    <div style="display: flex; justify-content: space-between;">
      <h3 style="margin-bottom:20px">{{label}}</h3>
    </div>

    <div>
      <div style="margin-top:10px" v-if="tab=='1'">
        <add-people-table title="寄住信息" :data="lodgePersonsTable"></add-people-table>
      </div>
      <div style="margin-top:10px"  v-if="tab=='2'">
        <add-pie-table title="寄住信息" :data="lodgePersonsTable"></add-pie-table>
      </div>
    </div>
 </div>
</template>

<script>
    import Service from '../service.js'
    import AddPeopleTable from './add-people-table'
    import AddPieTable from './add-pie-table'

    export default {
        components: { AddPeopleTable,AddPieTable },
        props: [],
        data() {
            return {
                tab:"",
                type: "",
                label:"",
                lodgePersonsTable: []
            }
        },
        methods: {
            getServer (type) {

                Service.queryMapCountList(type).then(res => {
                    console.log(res.data)
                    if (!res.data) return
                    if (!res.data.hdata) return
                    this.lodgePersonsTable = res.data.hdata ? res.data.hdata : []
                })
            },
            GetUrlParam(paraName) {
                let url = document.location.toString();
                let arrObj = url.split("?");
                if (arrObj.length > 1) {
                    let arrPara = arrObj[1].split("&");
                    let arr;
                    for (let i = 0; i < arrPara.length; i++) {
                        arr = arrPara[i].split("=");

                        if (arr != null && arr[0] == paraName) {
                            return arr[1];
                        }
                    }
                    return "";
                } else {
                    return "";
                }
            }
        },
        computed: {},
        mounted() {
            let type= this.GetUrlParam("type");
            let communityName = this.GetUrlParam("communityName");
            let apartmentNum = this.GetUrlParam("apartmentNum");
            if(type=='total'){
               this.tab='1'
               this.label='房屋总数'
            }else if(type=='self'){
                this.tab='1'
                this.label='自住套数'
            }else if(type=='rent'){
                this.tab='1'
                this.label='出租套数'
            }else if(type=='empty'){
                this.tab='1'
                this.label='空置套数'
            }else if(type=='todo'){
                this.tab='1'
                this.label='待核套数'
            }else if(type=='today'){
                this.tab='2'
                this.label='人口总数'
            }else if(type=='todayNumber'){
                this.tab='2'
                this.label='常住人口'
            }else if(type=='todayNumber2'){
                this.tab='2'
                this.label='暂住人口'
            }else if(type=='todayNumber3'){
                this.tab='2'
                this.label='寄住人口'
            }else if(type=='todayNumber4'){
                this.tab='2'
                this.label='其他人口'
            }
            this.getServer({"type":type,"communityName":decodeURIComponent(communityName),"apartmentNum":decodeURIComponent(apartmentNum)})
        }
    }
</script>

